// @import "node_modules/bootstrap-sass/assets/stylesheets/bootstrap";
.note-group-select-from-files {
  display: none;
}

body {
	font-family: 'Lato';
	background-color:#26C4C0;
	padding-top:75px;
	padding-bottom:25px;
}

.fa-btn {
	margin-right: 6px;
}

.container-3 {
	background-color:#26C4C0;
	left: 15px;
	position:static;
	width:70%;
	padding-bottom:25px;
	height:100%;
	margin-left:5px;
}

.container-2 {
	background-color:#26C4C0;
	right:13px;
	top:0px;
	position:absolute;
	width:28%;
	padding-top:75px;
	padding-bottom:25px;
	height:100%;
	margin-left:10px;
}

.panel-sort {
	left:0px; 
	position:static; 
	height:30px; 
	background-color:#333333; 
	color:white; 
	padding:5px; 
	padding-left:40px; 
	width:100%; 
	border-left: 1px solid blue; 
	border-right: 1px solid blue;
}

.changed-notification {
	bottom:0; 
	position:absolute; 
	border:0; 
	border-radius:0; 
	width:100%; 
	background-color: 
	#006dcc; 
	font-family:verdana; 
	border-top:1px solid #C4BDB6; 
	border-bottom: 1px solid #C4BDB6; 
	min-height:50px; 
	max-height:100%;
	text-align:center; 
	padding-top:15px; 
	color:#ffffff; 
	margin-bottom:0px;
}

.modal-header {
	color:#ffffff;
	background-color:#006dcc;
	border-top-left-radius: 5px;
	border-top-right-radius:5px;
}

.show-article-body {
	background-color:#333333;
	color:white;
	padding:50px;
	width:100%;
	height:100%;
	top:100px;
	position:static;
}

.navacp {
  height:50px; 
  width:100%; 
  top:0px; 
  left:0px; 
  position:fixed; 
  background-color:#23282e; 
  color:white; 
  font-size:20px; 
  padding:15px; 
  padding-left:50px;
  z-index:2;
}

.nav-side-menu {
  overflow: auto;
  font-family: verdana;
  font-size: 12px;
  font-weight: 200;
  background-color: #2e353d;
  position: fixed;
  top: 0px;
  width: 200px;
  height: 100%;
  color: #e1ffff;
  z-index:3;
}
.nav-side-menu .brand {
  background-color: #23282e;
  line-height: 50px;
  display: block;
  text-align: center;
  font-size: 14px;
}
.nav-side-menu .toggle-btn {
  display: none;
}
.nav-side-menu ul,
.nav-side-menu li {
  list-style: none;
  padding: 0px;
  margin: 0px;
  line-height: 35px;
  cursor: pointer;
  /*    
    .collapsed{
       .arrow:before{
                 font-family: FontAwesome;
                 content: "\f053";
                 display: inline-block;
                 padding-left:10px;
                 padding-right: 10px;
                 vertical-align: middle;
                 float:right;
            }
     }
*/
}
.nav-side-menu ul :not(collapsed) .arrow:before,
.nav-side-menu li :not(collapsed) .arrow:before {
  font-family: FontAwesome;
  content: "\f078";
  display: inline-block;
  padding-left: 10px;
  padding-right: 10px;
  vertical-align: middle;
  float: right;
}
.nav-side-menu ul .active,
.nav-side-menu li .active {
  border-left: 3px solid #d19b3d;
  background-color: #4f5b69;
}
.nav-side-menu ul .sub-menu li.active,
.nav-side-menu li .sub-menu li.active {
  color: #d19b3d;
}
.nav-side-menu ul .sub-menu li.active a,
.nav-side-menu li .sub-menu li.active a {
  color: #d19b3d;
  text-decoration:none;
}
.nav-side-menu ul .sub-menu li,
.nav-side-menu li .sub-menu li {
  background-color: #181c20;
  border: none;
  line-height: 28px;
  border-bottom: 1px solid #23282e;
  margin-left: 0px;
}
.nav-side-menu ul .sub-menu li:hover,
.nav-side-menu li .sub-menu li:hover {
  background-color: #020203;
}
.nav-side-menu ul .sub-menu li:before,
.nav-side-menu li .sub-menu li:before {
  font-family: FontAwesome;
  content: "\f105";
  display: inline-block;
  padding-left: 10px;
  padding-right: 10px;
  vertical-align: middle;
}
.nav-side-menu li {
  padding-left: 0px;
  border-left: 3px solid #2e353d;
  border-bottom: 1px solid #23282e;
}
.nav-side-menu li a {
  text-decoration: none;
  color: #e1ffff;
}

.nav-side-menu a li {
  text-decoration: none;
  color: #e1ffff;
}
.nav-side-menu li a i {
  padding-left: 10px;
  width: 20px;
  padding-right: 20px;
  text-decoration:none;
}

.nav-side-menu a li i {
  padding-left: 10px;
  width: 20px;
  padding-right: 20px;
  text-decoration:none;
}
.nav-side-menu li:hover {
  border-left: 3px solid #d19b3d;
  background-color: #4f5b69;
  -webkit-transition: all 1s ease;
  -moz-transition: all 1s ease;
  -o-transition: all 1s ease;
  -ms-transition: all 1s ease;
  transition: all 1s ease;
}
@media (max-width: 767px) {
  .nav-side-menu {
    position: relative;
    width: 100%;
    margin-bottom: 10px;
  }
  .nav-side-menu .toggle-btn {
    display: block;
    cursor: pointer;
    position: absolute;
    right: 10px;
    top: 10px;
    z-index: 10 !important;
    padding: 3px;
    background-color: #ffffff;
    color: #000;
    width: 40px;
    text-align: center;
  }
  .brand {
    text-align: left !important;
    font-size: 22px;
    padding-left: 20px;
    line-height: 50px !important;
  }
}
@media (min-width: 767px) {
  .nav-side-menu .menu-list .menu-content {
    display: block;
  }
}

